  .animated-text{
    font-size: 44px;
    font-weight: 600;
    min-width: 280px;
    margin-top: 100px;
  }
  
  .animated-text span{
    position: relative;
  }
  
  
  .animated-text span::after{
    content: "";
    position: absolute;
    width: calc(100% + 8px);
    height: 100%;
    background-color: white;
    border-left: 2px solid #ff6f5c;
    right: -8px;
    animation: cursor .8s infinite, typing 20s steps(14) infinite;
  }
  
  @keyframes cursor {
    to{
        border-left: 2px solid #ff6f5c00;
    }
  }
  
  
  @keyframes typing {
    10%,15%,30%,35%,50%,55%,70%,75%,90%,95%{
        width: 0;
    }
    5%,20%,25%,40%,45%,60%,65%,80%,85%{
        width: calc(100% + 8px);
    }
  }
  
  
